ght.ttf');\n}\n\n#container {\n    height: 100vh;\n    display: grid;\n    grid-template-rows: 1fr 8fr;\n    grid-template-columns: 1fr 4fr;\n\tfont-family: var(--regfont);\n\tcolor: var(--slate);\n}\n\nbutton {\n\tfont-family: var(--regfont);\n\tcolor: var(--slate);\n\tcursor: pointer;\n\tborder: none;\n\tborder-radius: 5px;\n}\n\nlabel {\n\tfont-size: 18px;\n}\n\ninput {\n\theight: 25px;\n\tfont-size: 15px;\n\tborder: 2px solid var(--indigo);\n\tbackground-color: var(--ghostbanana);\n\tborder-radius: 5px;\n}\n\ninput:focus {\n\toutline-color: var(--deepindigo);\n}\n\ninput[type=date] {\n\tfont-family: var(--regfont);\n}\n\n.newButton {\n\tbackground-color: var(--banana);\n}\n\n.newButtonIcon {\n\tpadding-right: 3px;\n\tfont-weight: 700;\n}\n\n.inputButton {\n\tpadding: 8px;\n}\n\n.addButton {\n\tbackground-color: var(--green);\n}\n\n.cancelButton {\n\tbackground-color: var(--red);\n}\n\n/* HEADER STYLE */\n\n\n#header {\n    grid-area: 1 / 1 / 2 / 3;\n\tcolor: var(--offwhite);\n    background-color: var(--strongblue);\n\tdisplay: grid;\n\tgrid-template-columns: 1fr 4fr 1fr;\n\tjustify-items: center;\n\talign-items: center;\n}\n\n#headerTitle {\n\tfont-size: 60px;\n\tfont-weight: 400;\n\tfont-family: var(--titlefont);\n}\n\n#sidebarToggle {\n\tbackground-image: url(\"media/bars.svg\");\n\theight: 40px;\n\twidth: 40px;\n\tborder: none;\n\tbackground-color: transparent;\n\tfilter: invert(14%) sepia(10%) saturate(2318%) hue-rotate(179deg) brightness(91%) contrast(93%);\n}\n\n#githubLink {\n\theight: 24px;\n\twidth: 24px;\n\tmargin: 5px;\n\tbackground-repeat: no-repeat;\n\tbackground-image: url('media/github.svg');\n\tfilter: invert(14%) sepia(10%) saturate(2318%) hue-rotate(179deg) brightness(91%) contrast(93%);\n\ttransition: 0.3s;\n}\n\n#githubLink:hover {\n\tfilter: invert(81%) sepia(52%) saturate(326%) hue-rotate(357deg) brightness(104%) contrast(98%);\n}\n\n/* SIDEBAR STYLE */\n\n\n#sidebar {\n    grid-area: 2 / 1 / 3 / 2;\n    background-color: var(--coolblue);\n\tdisplay: grid;\n\tgrid-auto-rows: min-content;\n\tgap: 15px;\n\tpadding: 25px;\n\tmin-width: 217px;\n}\n\n#sidebarMain {\n\tdisplay: grid;\n\tgrid-auto-rows: min-content;\n\tgap: 5px;\n}\n\n.projectImg {\n\theight: 22px;\n\twidth: 22px;\n\tjustify-self: end;\n}\n\n#PriorityImg {\n\tbackground-image: url(\"media/star.svg\");\n\tfilter: invert(14%) sepia(10%) saturate(2318%) hue-rotate(179deg) brightness(91%) contrast(93%);\n}\n\n#AllImg {\n\tbackground-image: url(\"media/home.svg\");\n\tfilter: invert(14%) sepia(10%) saturate(2318%) hue-rotate(179deg) brightness(91%) contrast(93%);\n}\n\n#projectTitle {\n\tfont-size: 26px;\n\tfont-weight: 700;\n\tmargin-top: 10px;\n}\n\n#newProjectButton {\n\twidth: fit-content;\n\tfont-size: 15px;\n\tpadding: 8px;\n\tbox-shadow: 2px 2px var(--slate);\n}\n\n#projectPrompt {\n\tdisplay: none;\n\tbackground-color: var(--greyblue);\n\tborder-radius: 5px;\n}\n\n#projectInput {\n\tdisplay: flex;\n\tflex-direction: column;\n\tpadding: 5px;\n\tgap: 5px;\n}\n\n#projectContainer {\n\tdisplay: flex;\n\tflex-direction: column;\n\tgap: 10px;\n}\n\n.project {\n\tpadding: 10px;\n\tmin-height: 44px;\n\tborder-radius: 5px;\n\tbackground: none;\n\tborder: none;\n\tdisplay: grid;\n\tgrid-template-columns: 1fr 1fr;\n\talign-items: center;\n\tjustify-items: baseline;\n\tfont-size: 20px;\n}\n\n.project:hover {\n\tbackground-color: var(--darkcoolblue);\n}\n\n.activeProject {\n\tbackground-color: var(--darkcoolblue);\n\tfont-weight: 700;\n}\n\n.projectDelete {\n\tdisplay: none;\n\tbackground-image: url(\"media/delete.svg\");\n\tbackground-color: transparent;\n\theight: 18px;\n\twidth: 18px;\n\tjustify-self: end;\n\tgrid-column: 2 / 3;\n\tfilter: invert(14%) sepia(10%) saturate(2318%) hue-rotate(179deg) brightness(91%) contrast(93%);\n}\n\n\n/* MAIN STYLE */\n\n#main {\n\tbackground-color: var(--offwhite);\n\tpadding: 20px;\n\tdisplay: grid;\n\tgrid-auto-rows: min-content;\n\tgap: 20px;\n}\n\n#mainTitle {\n\theight: 40px;\n\tfont-size: 40px;\n\tfont-weight: 700;\n\tcolor: var(--offwhite);\n\tbackground-color: var(--indigo);\n\tpadding: 50px;\n\tborder-radius: 5px;\n}\n\n#newTaskButton {\n\tfont-size: 18px;\n\tborder-radius: 5px;\n\twidth: fit-content;\n\tpadding: 10px;\n\tbox-shadow: 2px 2px var(--slate);\n}\n\n#taskPrompt {\n\tdisplay: none;\n\tcolor: var(--slate);\n\tbackground-color: var(--lightindigo);\n\tpadding: 20px;\n\tborder-radius: 5px;\n}\n\n#taskInput {\n\tdisplay: flex;\n\tflex-direction: column;\n\tgap: 8px;\n}\n\n#taskInput > input {\n\tmargin-bottom: 4px;\n}\n\n.promptButtonContainer {\n\talign-self: center;\n}\n\n.newTaskContainer {\n\tbackground-color: var(--easyblue);\n\tdisplay: grid;\n\tgrid-template-rows: repeat(2, min-content);\n\tgrid-template-columns: 1fr 5fr 2fr repeat(2, 1fr);\n\tborder-radius: 5px;\n\talign-items: center;\n\tpadding: 8px;\n\tmargin-bottom: 20px;\n\tmin-height: 46px;\n\tmax-height: 46px;\n\toverflow: hidden;\n\ttransition: all 0.8s ease-in-out;\n}\n\n.newTaskContainer:hover, .newTaskContainer:active {\n\theight: fit-content;\n\tmax-height: 1000px;\n}\n\n.taskName {\n\tfont-weight: 700;\n\tfont-size: 18px;\n}\n\n.taskCheck {\n\tgrid-area: 1 / 1 / 2 / 2;\n\tjustify-self: center;\n}\n\n.taskName {\n\tgrid-column: 2 / 3;\n}\n\n.taskDesc {\n\tgrid-area: 2 / 2 / 3 / 3;\n}\n\n.taskCheck, .taskDelete, .taskPriority {\n\theight: 30px;\n\twidth: 30px;\n\tborder: none;\n\tbackground-color: transparent;\n\tbackground-position: center;\n\tbackground-repeat: no-repeat;\n\tfilter: invert(14%) sepia(10%) saturate(2318%) hue-rotate(179deg) brightness(91%) contrast(93%);\n}\n\n.taskPriority {\n\tbackground-image: url(\"media/star.svg\");\n\t/* color should be slightly darker than background, so it looks indented */\n}\n\n.taskDelete {\n\tbackground-image: url(\"media/delete.svg\");\n}\n\n.taskCheck {\n\tbackground-image: url(\"media/check.svg\");\n}\n\n.taskPriorityActive {\n\tfilter: invert(38%) sepia(86%) saturate(3949%) hue-rotate(272deg) brightness(105%) contrast(87%);\n}\n\n.completedTask {\n\topacity: 50%;\n}\n\n.completedTask > p:first-child {\n\ttext-decoration: line-through 2px;\n}\n\n.completedTask > .taskCheck {\n\tbackground-image: url(\"media/checkfull.svg\");\n\tfilter: invert(38%) sepia(86%) saturate(3949%) hue-rotate(272deg) brightness(105%) contrast(87%);\n}\n\n/* MEDIA QUERY */\n\n@media only screen and (max-width: 600px) {\n\t#container {\n\t\tdisplay: flex;\n\t\tflex-direction: column;\n\t\n\t}\n\t#main {\n\t\theight: 100%;\n\t}\n}"],"sourceRoot":""}]);
